*, *::after {
box-sizing: border-box;
}

.container {
display: -webkit-flex;
display: flex;
}

.hidden {
display: none;
}

body {
font-family: 'Helvetica Neue', Arial, sans-serif;
}

.grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas: "userComponent orgs orgs orgs" "userComponent orgs orgs orgs" "userComponent orgs orgs orgs";
}

.userComponent {
    grid-area: userComponent;
    height: 900px;
    position: -webkit-sticky;
    position: sticky;
    top:0;
}

.orgs { grid-area: orgs; }


.App {
  text-align: center;
}

.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
}

html, body { height: 100%; }

#root{
  background-color: #770097;
  height: 100%;
}


.home {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-areas:  "userComponent orgs orgs orgs orgs" "userComponent orgs orgs orgs orgs" "userComponent orgs orgs orgs orgs";
}

.userComponent {
  grid-area: userComponent;
  background-color: #770097;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
  color:white;

}

.Navigation{
  width: 100%;
  text-align: right;
  height: 40vh;

}

.Navigation ul{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  height: 40vh;

}

.Navigation li{

  list-style-type: none;
  font-size: 2rem;
  line-height: 0px;
  font-weight: bold;
  height: 4rem;
  margin-right: 30px;





}


.Navigation li a:visited{
  color: white;
  transition: color .9s;
}

.Navigation li a:visited:hover{
  color: #009778;


}


.userInfo{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  bottom: 50px;


}

.userName{
  position: relative;
  bottom: 15px;

}
.userComponent img{
  border-radius:150px;
  border: 6px solid #009778;
}


.orgs { grid-area: orgs;
      display: -webkit-flex;
      display: flex;
      width:100%;
      -webkit-flex-wrap: wrap;
              flex-wrap: wrap;
      -webkit-align-content: space-around;
              align-content: space-around;
      -webkit-justify-content: space-around;
              justify-content: space-around;
      padding: 20px;
      background-color:white;
}

.pageHeader div{
  width: 100vw;

}

#ui:first-child{
margin-top: 1em;
}

#ui:last-child{
  margin-bottom: 1em;
}


