*, *::after {
box-sizing: border-box;
}

.container {
display: flex;
}

.hidden {
display: none;
}

body {
font-family: 'Helvetica Neue', Arial, sans-serif;
}

.grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas: "userComponent orgs orgs orgs" "userComponent orgs orgs orgs" "userComponent orgs orgs orgs";
}

.userComponent {
    grid-area: userComponent;
    height: 900px;
    position: sticky;
    top:0;
}

.orgs { grid-area: orgs; }

